import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

let router = new Router({
	routes: [
		{
			path:'/',
			component: () => import('@/layout/Index')
		},
		{
			path: '/layout1',
			component: () => import('@/views/layout1'),
			children:[
				{path:'users',component:()=>import('@/views/layout1/users')},
				{path:'userInfo/:id',component:()=>import('@/views/layout1/userInfo'),props:true},
				{path:'rights',component:()=>import('@/views/layout1/rights')},
				{path:'goods',component:()=>import('@/views/layout1/goods')},
				{path:'orders',component:()=>import('@/views/layout1/orders')},
				{path:'settings',component:()=>import('@/views/layout1/settings')},
			]
		},
		{
			path: '/login',
			component: () => import('@/views/login')
		},
		{
			path: '/slide1',
			component: () => import('@/views/demo/slide1')
		},
		{
			path: '/slide2',
			component: () => import('@/views/demo/slide2')
		},
		
	]
})



export default router
